@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

*{
  font-family: 'Lato', sans-serif;
  letter-spacing: .5px;
}
html{
  scroll-behavior: smooth;
  font-size: 15px;
}
body{
  margin: 0;
  background-color: rgb(230, 230, 230);
}

h1, h2, h3, button, input, div {
  color: #111;
}
p, li{
  color: #212121;
}

.App {
  text-align: center;
}

.search-bar{
  border: 0;
  font-size: 1.5rem;
  margin: 30px 0px;
  padding: 10px 15px 1px 10px;
  border-bottom: 3px solid black;
  background-color: transparent;
  transition: .3s
}

.search-bar:focus{
  outline: none;
  border-bottom: 3px solid rgb(49, 151, 80);
}

.search-button{
  font-size: 1.5rem;
  background: none;
  outline: none;
  border: 3px solid black;
  transition: .2s;
}
.search-button:hover{
  cursor: pointer;
  background-color: #fff;
}


@media(max-width:700px){
  .search-bar{
    padding: 10px 10px 1px 10px;
    width: 55%;
  }
  .search-button{
    width: 30%;
  }
}