

.nav{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 10px;

    max-width: 1000px;
    margin: 20px auto 0px;
    padding: 10px;
}

.pageBox{
    position: relative;
    filter: grayscale(50%);
    transition: .2s;
}
.pageBox:hover{
    filter: grayscale(0);
    cursor: pointer;
    outline: 5px solid rgba(0, 0, 0, 0.616);
    z-index: 1;
}
.pageTitle{
    font-size: 1.5rem;
    color: #000;
    text-shadow: 0 0 2px rgb(255, 255, 255);
    background-color: rgba(255, 255, 255, 0.5);
    width: 100%;
    padding: 0;
    margin: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.pageImg{
    width: 100%;
    height: 100%;
}

@media(max-width:900px){
    .pageTitle{ font-size: 1rem; }
}
@media(max-width: 700px){
    .nav{
        margin: 0 auto;
        box-sizing: border-box;
        width:100%;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(5, 1fr);
    }
    .pageTitle{
        font-size: 1.5rem;
    }
}