.scale{
    margin-top: 15px;
    width: 100%;
    text-align: start;
    font-size: 1rem;
}
.bar{
    width:100%;
    height: 30px;
	overflow: hidden;
    background: rgb(255, 255, 255);
    border-radius: 99px;
}

.percentage{
	width: 0%;
	height:100%;

    /* starting position */
	background: hsl(0, 100%, 79%);
    transition: .5s;
}

p{
	margin: 0
}