*{
  font-family: 'Lato', sans-serif;
  letter-spacing: .5px;
}
html{
  scroll-behavior: smooth;
  font-size: 15px;
}
body{
  margin: 0;
  background-color: rgb(230, 230, 230);
}

h1, h2, h3, button, input, div {
  color: #111;
}
p, li{
  color: #212121;
}

.App_App__1zmjt {
  text-align: center;
}

.App_search-bar__2FPni{
  border: 0;
  font-size: 1.5rem;
