
:root{
    --imgSize:80px;
    --marosTagsWidth:310px;
}


/* MAIN SECTION */
.main{
    margin: 0 auto;
    width: 90%;
    max-width: 1300px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

/* MAKROS */
.makros{
    flex-shrink: 0;
    width: var(--marosTagsWidth);
}
.makros > h2{ font-size: 1.5rem; }
.porActiv{
    outline: 1px solid black;
    cursor: pointer;
}
.porNotActiv{
    cursor: pointer;
}
.porActiv, .porNotActiv{ transition: .2s; }
.porActiv:hover, .porNotActiv:hover{ background-color: #fff; }

.makrosDiv{
    overflow: hidden;
    transition: 1s;
}
.makroSwitch{
    margin-top: 15px;
    border: 4px solid #345;
    border-radius: 99px;

}

.makroSwitchButton{
    margin-top: 15px;
    padding: 5px;
    border: 1px solid black;
    border-radius: 5px;
    font-size: 20px;
    letter-spacing:0.5px;
    transition:.2s
}
.makroSwitchButton:hover{
    cursor: pointer;
    background-color: #fff;
}
.mSBCollapse:hover{
    letter-spacing:-0.5px;
}
.mSBExpand:hover{
    letter-spacing:2px;
}


/* INFO SECTION */
.info{
    margin:0 20px;
}
.info > h1{ font-size: 2.5rem; }
.info > h2{ font-size: 1.5rem; }
.ing-ol{
    font-size: 1rem;
    list-style: none;
    padding-left: 0;
    text-align: start;
}
.div-li{
    display: flex;
    margin: 10px 0;
    align-items: center;
}
.img-li{
    border: 2px solid #333;
    display: inline-block;
    width: var(--imgSize);
    height: var(--imgSize);
    border-radius: 50%;
    margin: 0 10px;
}



/* SEARCH TAGS */
.searchTags{
    width: var(--marosTagsWidth);
    flex-shrink: 0;
}
.searchTags > h2{ font-size: 1.5rem; }
.foodCat, .healthLabel{
    display: flex;
    flex-wrap: wrap;
}
.foodCat p, .healthLabel p{
    font-size: 1rem;
    margin: 5px;
    padding: 5px;
    border: 2px solid #333;
    border-radius: 15px;
    transition: .2s;

}
.foodCat p:hover, .healthLabel p:hover{
    cursor: pointer;
    border-radius: 5px;
    background-color: #fff;
}


@media(max-width:1200px){
    .main{
        width: 100%;
        display: grid;
        grid-template-columns: 350px 1fr;
    }
    .makros{
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
    }
    .info{
        padding: 0 20px;
        margin: 0;
        box-sizing: border-box;
    }
    .searchTags{
        margin: 30px 0;
        width: 90%;
        grid-column: 1/span 2;
        justify-self: center;
    }

}
@media(max-width:700px){
    .main{
        display: flex;
        flex-direction: column;
    }
    .makros{
        background-color: rgb(216, 216, 216);
        order: 2;
        width: 100%;
        padding: 0px 10px;
        margin: 60px 0 0 0;
        box-sizing: border-box;
    }
    .info{
        order: 1;
        width: 100%;
        margin: 0;
    }
    .searchTags{
        order:3;
        width: 100%;
        margin: 50px 0 60px 0;
    }
}


